import Vue from 'vue'
import Vuex from 'vuex'
import Axios from 'axios'
import {actionType} from '@/assets/js/actionType'
import config from './modules/config'
import write from './modules/write'
import userInfo from './modules/userInfo'

Vue.use(Vuex)

const state = {
  token: '',
  code: '',
  detail: {
    title: ''
  }
}
const getters = {}
const mutations = {
  [actionType.set_token](state, payload) {
    state.token = payload
  },
  [actionType.set_detail_title](state, payload) {
    state.detail.title = payload.title
  },
}
const actions = {}

export default new Vuex.Store({
  state,
  getters,
  actions,
  mutations,
  modules: {
    config,
    userInfo,
    write
  }
})



